import { Component, OnInit } from '@angular/core';
import { User } from  '../user';
import { Location } from '@angular/common';
import { RestService } from "../rest.service";

@Component({
  selector: 'app-user-form',
  templateUrl: './user-form.component.html',
  styleUrls: ['./user-form.component.css']
})
export class UserFormComponent implements OnInit {

  public user: User = {
    f_name: '',
    l_name: '',
    email: '',
    password: '',
    role: 0,
    team: 0,
    skill_set: []
  };

  private isEmployee: boolean;
  errMsg: string;
  resMsg: string = '';

  public roles: any[];
  public teams: any[];
  public tasks: any[];

  public tasksMap: any = {};
  public checkedTasks: any[] = [];

  constructor(private restService: RestService,
              private location: Location) {
  }

  ngOnInit() {


    this.restService.send('get', '/getdata')
        .then((res) => {
          //console.log('res => ', res);
          let result = res[0].allData;
          if (res[0].success) {

            this.roles = result.roles.reverse();
            this.teams = result.teams;
            this.tasks = result.tasks;

            for (let task of this.tasks) {
              this.tasksMap[task.id] = false;
            }

            this.user = {
              f_name: '',
              l_name: '',
              email: '',
              password: '',
              role: this.roles[0].id,
              team: this.teams[0].id,
              skill_set: this.tasks
            };

            if (this.user.role == 3) this.isEmployee = true;
          }
          else {
            this.errMsg = res[0].msg;
          }
        })
        .catch((ex) => {
          console.log('error.statusText -> ', ex);
          this.errMsg = ex;
        });

  }

  private onClick(role) {
    //role.id == 3 --> role.id == Employee
    role.id == 3 ? this.isEmployee = true : this.isEmployee = false;
  }

  public updateCheckbox(skill, event) {
    this.tasksMap[skill.id] = event.target.checked;
  }

  public updateTasks() {
    for (let x in this.tasksMap) {
      if (this.tasksMap[x]) {
        this.checkedTasks.push(x);
      }
    }
  }

  goBack(): void {
    this.location.back();
  }

  public save(user: User, isValid: boolean) {

    console.log('isValid => ', isValid);

    if (isValid) {
      if (this.isEmployee) {
        this.updateTasks();
        user.skill_set = this.checkedTasks;
        this.checkedTasks = [];

        for (let task of this.tasks) {
          if (task.spec_id == 1) user.skill_set.push(task.id.toString());
        }

        user.team = +user.team;
      }

      console.log(user);


      let token = window.localStorage.getItem('auth_key');
      let body = `token=${token}&user=${JSON.stringify(user)}`;

      this.restService.send('post', '/adduser', body)
          .then((res) => {
            //console.log(res);
            let response = res[0];

            if (response.success) {
              this.resMsg = response.msg;
              setTimeout(() => {
                //noinspection TypeScriptUnresolvedFunction
                window.location.reload()
              }, 1000);
            }
            else if (!response.success) {
              this.errMsg = response.msg;
            }

            setTimeout(() => {
              this.resMsg = ''
            }, 5000)
          })
          .catch((ex) => {
            console.log('error.statusText -> ', ex);
            this.errMsg = ex;
          });

    }
  };
}



