import { Component, OnInit, OnDestroy } from '@angular/core';
import { RestService } from "../rest.service";

@Component({
  selector: 'app-status-table',
  templateUrl: './status-table.component.html',
  styleUrls: ['./status-table.component.css']
})
export class StatusTableComponent implements OnInit, OnDestroy {

  private errMsg: string;
  private lastUpdate: any;
  private timer: any;
  private category: string = '';
  private clicksCount: number = 0;
  private ascending: boolean = true;
  private users: any[] = [];
  private statistics: any[] = [];

  constructor(private restService: RestService) {
  }

  private getStatuses(): void {
    this.lastUpdate = new Date();

    this.restService.send('get', '/status')
        .then((res) => {
          //console.log(res);
          let response = res[0];

          if (response.success) {
            this.users = response.allData.users;
            this.statistics = response.allData.statistics;
            this.errMsg = '';
          }
          else if (!response.success && !response.allData) {
            this.users = [];
            this.statistics = [];
            this.errMsg = response.msg;
          }
          else if (!response.success) {
            this.users = response.allData.users;
            this.errMsg = response.msg;
          }
        })
        .catch((ex) => {
          console.log('error.statusText -> ', ex);
          this.errMsg = ex;
        });
  }

  ngOnInit() {

    this.getStatuses();

    this.timer = setInterval(() => {
      this.getStatuses();
    }, 5000);

  }

  setCategory(cat: string): void {
    //console.log('cat => ', cat);
    this.category = cat;
    ++this.clicksCount;
    (this.clicksCount % 2 == 0) ? this.ascending = false : this.ascending = true;
  }

  ngOnDestroy() {
    clearInterval(this.timer);
    //console.log('Component has been destroyed');
  }

}



