import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'nameTeamPipe'
})
export class NameTeamPipe implements PipeTransform {

  transform(array: Array<any>, args: any): Array<any> {

    //console.log('array => ', array);
    //console.log('args => ', args);

    let last_name = args.last_name;
    let team = args.team;

    if (last_name == '' && team == '') return array;

    array = array.filter(user => {
      return user.last_name.toLowerCase().indexOf(last_name.toLowerCase()) >= 0;
    });

    array = array.filter(user => {
      return user.team_title.toLowerCase().indexOf(team.toLowerCase()) >= 0;
    });

    return array;

  }

}



