import {Injectable} from "@angular/core";
import {CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot} from "@angular/router";
import {TokenParseService} from "./token-parse.service";
import {routerNgProbeToken} from "@angular/router/src/router_module";

@Injectable()
export class AuthManager implements CanActivate {
  errMsg: string = '';

  constructor(private router: Router,
              private tokenService: TokenParseService) {
  }

  canActivate(next: ActivatedRouteSnapshot,
              state: RouterStateSnapshot) {

    if (next.url[0].path == 'login') {
      if (window.localStorage.getItem('auth_key')) {
        this.errMsg = 'You are already logged in! ';
        console.log(this.errMsg);
        // alert(this.errMsg + 'Please Logout');

        switch (this.tokenService.getRole()) {
          case "1":
            this.router.navigate(['/admin']);
            break;
          case "2":
            this.router.navigate(['/supervisor']);
            break;
          case "3":
            this.router.navigate(['/employee']);
        }

        return false;
      }
      else {
        return true;
      }
    }

    //todo check role from auth_key
    if (window.localStorage.getItem('auth_key')) {
      return true;
    }

    console.log('You must be logged in');

    this.router.navigate(['/login']);
    return false;
  }
}


