import {Component, OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {TokenParseService} from "../token-parse.service";
import {RestService} from "../rest.service";

@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.css']
})
export class UserListComponent implements OnInit {

  private users: any;
  private role: string;
  private resMsg: string;
  private sure: boolean;

  constructor(private restService: RestService,
              private router: Router,
              private tokenParse: TokenParseService) {
  }

  ngOnInit() {

    this.role = this.tokenParse.getRole();
    //console.log('role => ', this.role);

    this.restService.send('get', '/users').then((res) => {
      //console.log(res);
      let response = res[0];

      if (response.success) {
        this.users = response.users;
      }
      else {
        this.resMsg = response.msg;
      }
    }).catch((ex) => {
      console.log('error.statusText -> ', ex);
      this.resMsg = ex;
    });

  }

  deleteUser(user: any) {
    let user_id = user.id;
    console.log('user_id => ', user_id);

    this.sure = confirm(`Do you really want to change state of ${user.first_name} ${user.last_name} to inactive ?`);

    if (this.sure) {

      this.restService.send('put', `/deleteuser/:${user_id}:0`).then((res) => {
        //console.log(res);
        let response = res[0];

        this.resMsg = response.msg;

        if (response.success) {
          console.log(this.resMsg);
          this.ngOnInit();
          setTimeout(() => location.reload(), 500)
        }
      }).catch((ex) => {
        console.log('error.statusText -> ', ex);
        this.resMsg = ex;
      });

    }

  }

  editUser(user_id: number) {
    console.log('user_id => ', user_id);

    this.router.navigate(['/edituser', user_id]);
  }

}



