import {Component, OnInit} from '@angular/core';
import {FormControl, FormGroup, FormBuilder} from '@angular/forms';
import {Router} from '@angular/router';
import {Cookie} from 'ng2-cookies/ng2-cookies';
import {RestService} from "../rest.service";
import { NavbarComponent } from "../navbar/navbar.component";

@Component({
  selector: 'app-log-in',
  templateUrl: './log-in.component.html',
  styleUrls: ['./log-in.component.css']
})

export class LogInComponent implements OnInit {
  private userMail: FormControl;
  private userPassword: FormControl;
  private userRemember: FormControl;
  private userForm: FormGroup;
  private errMsg: string;

  constructor(private router: Router,
              private fb: FormBuilder,
              private restService: RestService) {
  }

  ngOnInit() {
    this.userMail = this.fb.control(Cookie.get('mailCookie'));
    this.userPassword = this.fb.control(Cookie.get('passwordCookie'));
    this.userRemember = this.fb.control(false);

    this.userForm = this.fb.group({
      mail: this.userMail,
      password: this.userPassword,
      remember: this.userRemember,
    });

  }

  login() {
    if (this.userForm.value.remember) this.makeCookie();

    let body = `mail=${encodeURIComponent(this.userForm.value.mail)}&password=${encodeURIComponent(this.userForm.value.password)}`;

    this.restService.send('post', '/authenticate', body)
      .then((res) => {
        //console.log(res);
        let response = res[0];

        if (response.success) {
          window.localStorage.setItem('auth_key', response.token);

          switch (response.role) {
            case 1:
              this.router.navigate(['/admin']);
              NavbarComponent.returned.next(false);
              break;
            case 2:
              this.router.navigate(['/supervisor']);
              NavbarComponent.returned.next(false);
              break;
            case 3:
              this.router.navigate(['/employee']);
              NavbarComponent.returned.next(false);
              break;
          }

          }
          else if (!response.success) {
            this.errMsg = response.msg;
          }
        })
        .catch((ex) => {
          console.log('error.statusText -> ', ex);
          this.errMsg = ex;
        });

  }

  makeCookie() {
    Cookie.set('mailCookie', this.userForm.value.mail, 3);
    Cookie.set('passwordCookie', this.userForm.value.password, 3);
  }

}



