import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Location } from '@angular/common';
import { Supervisor } from "../supervisor";
import { RestService } from "../rest.service";

@Component({
  selector: 'app-edit-sv',
  templateUrl: './edit-sv.component.html',
  styleUrls: ['./edit-sv.component.css']
})
export class EditSvComponent implements OnInit {

  private user_id: number;
  private currentSv: any;
  private errMsg: string;
  resMsg: string = '';

  public user: Supervisor = {
    id: 0,
    f_name: '',
    l_name: '',
    email: '',
    password: ''
  };

  constructor(private route: ActivatedRoute,
              private location: Location,
              private restService: RestService) {
  }

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.user_id = +params["id"];
    });
    console.log('this.user_id => ', this.user_id);

    let body = 'user_id=' + this.user_id;

    this.restService.send('post', '/svinfo', body)
        .then((res) => {
          //console.log(res);
          let response = res[0];

          if (response.success) {
            this.currentSv = response.data;

            //console.log('this.currentSv => ', this.currentSv);
            this.user = {
              id: this.user_id,
              f_name: this.currentSv.first_name,
              l_name: this.currentSv.last_name,
              email: this.currentSv.email,
              password: this.currentSv.password
            };
          }
          else {
            this.errMsg = response.msg;
          }
        })
        .catch((ex) => {
          console.log('error.statusText -> ', ex);
          this.errMsg = ex;
        });

  }

  goBack(): void {
    this.location.back();
  }

  public save(user: Supervisor, isValid: boolean) {

    console.log('isValid => ', isValid);

    if (isValid) {
      user.id = this.user_id;
      //console.log(user);


      let body = `user=${JSON.stringify(user)}`;

      this.restService.send('put', '/updatesv', body)
          .then((res) => {
            //console.log(res);
            let response = res[0];

            this.resMsg = response.msg;

            if (response.success) {
              /*this.user.f_name = '';
               this.user.l_name = '';
               this.user.email = '';
               this.user.password = '';*/

              setTimeout(() => {
                this.goBack()
              }, 2000);
            }

            setTimeout(() => {
              this.resMsg = ''
            }, 5000)
          })
          .catch((ex) => {
            console.log('error.statusText -> ', ex);
            this.errMsg = ex;
          });

    }
  };
}



