import { Component, OnInit } from '@angular/core';
import { SimpleUser } from "../simpleUser";
import { RestService } from "../rest.service";

@Component({
  selector: 'app-skills-global',
  templateUrl: './skills-global.component.html',
  styleUrls: ['./skills-global.component.css']
})
export class SkillsGlobalComponent implements OnInit {

  private resMsg: string;
  private errMsg: string;
  private sure: boolean;

  private users: any[] = [];
  private tasks: any[] = [];
  private checkedTasks: any[] = [];

  private category: string = '';
  private clicksCount: number = 0;
  private ascending: boolean;

  public simpleUser: SimpleUser = {
    id: 0,
    skill_set: []
  };

  constructor(private restService: RestService) {
  }

  ngOnInit() {

    //getting all users and their skills from DB
    this.restService.send('get', '/usersinfo')
        .then((res) => {
          //console.log('res => ', res);
          let result = res[0].allData;
          if (res[0].success) {
            this.users = result.users;

            //getting all tasks from DB
            this.restService.send('get', '/getdata')
                .then((res) => {
                  //console.log('res => ', res);
                  let result = res[0].allData;
                  if (res[0].success) {
                    this.tasks = result.tasks;
                  }
                  else {
                    this.errMsg = res[0].msg;
                  }
                })
                .catch((ex) => {
                  console.log('error.statusText -> ', ex);
                  this.errMsg = ex;
                });
          }
          else {
            this.errMsg = res[0].msg;
          }
        })
        .catch((ex) => {
          console.log('error.statusText -> ', ex);
          this.errMsg = ex;
        });

  }

  private updateCheckbox(user, task, event) {
    user.skillsMap[task.id] = event.target.checked;
    //console.log('user.skillsMap => ', user.skillsMap);
  }

  private updateTasks(skillsMap) {
    for (let x in skillsMap) {
      if (skillsMap[x]) {
        this.checkedTasks.push(x);
      }
    }
  }

  private setCategory(cat: string): void {
    //console.log('cat => ', cat);
    this.category = cat;
    ++this.clicksCount;
    (this.clicksCount % 2 == 0) ? this.ascending = false : this.ascending = true;
  }

  private updateUser(user) {
    this.sure = confirm(`Do you really want to update skills of ${user.first_name} ${user.last_name} ?`);

    if (this.sure) {
      this.simpleUser.id = user.id;

      this.updateTasks(user.skillsMap);
      this.simpleUser.skill_set = this.checkedTasks;
      this.checkedTasks = [];

      //console.log(this.simpleUser);
      let body = `user=${JSON.stringify(this.simpleUser)}`;

      this.restService.send('put', '/updateskills', body)
          .then((res) => {
            //console.log(res);
            let response = res[0];

            if (response.success) {
              this.resMsg = response.msg;
            }
            else if (!response.success) {
              this.errMsg = response.msg;
            }

            setTimeout(() => {
              this.ngOnInit();
            }, 1000)

            setTimeout(() => {
              this.resMsg = '';
            }, 3000)
          })
          .catch((ex) => {
            console.log('error.statusText -> ', ex);
            this.errMsg = ex;
          });
    }
    else{
      this.ngOnInit();
    }

  }

}



