import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {AdminComponent} from '../admin/admin.component';
import {AuthManager} from '../authmanager';
import {EmployeeComponent} from '../employee/employee.component';
import {LogInComponent} from '../log-in/log-in.component';
import {SupervisorComponent} from '../supervisor/supervisor.component';
import {EditUserComponent} from "../edit-user/edit-user.component";
import {EditSvComponent} from "../edit-sv/edit-sv.component";
import {UserFormComponent} from "../user-form/user-form.component";
import {SvGuard} from "../sv-guard";
import {UserGuard} from "../user-guard";
import {AdminGuard} from "../admin-guard";
import {AdminListsComponent} from "../admin-lists/admin-lists.component";
import {AdminSkillsComponent} from "../admin-skills/admin-skills.component";
import {SvListsComponent} from "../sv-lists/sv-lists.component";
import {SvSkillsComponent} from "../sv-skills/sv-skills.component";

const routes: Routes = [
  {path: '', redirectTo: '/login', pathMatch: 'full'},
  {path: 'login', component: LogInComponent, canActivate: [AuthManager]},
  {path: 'admin', component: AdminComponent, canActivate: [AuthManager, AdminGuard]},
  {path: 'editskills', component: AdminSkillsComponent, canActivate: [AuthManager, AdminGuard]},
  {path: 'sveditskills', component: SvSkillsComponent, canActivate: [AuthManager, SvGuard]},
  {path: 'listall', component: AdminListsComponent, canActivate: [AuthManager, AdminGuard]},
  {path: 'svlistall', component: SvListsComponent, canActivate: [AuthManager, SvGuard]},
  {path: 'adduser', component: UserFormComponent, canActivate: [AuthManager, AdminGuard]},
  {path: 'employee', component: EmployeeComponent, canActivate: [AuthManager, UserGuard]},
  {path: 'supervisor', component: SupervisorComponent, canActivate: [AuthManager, SvGuard]},
  {path: 'editsv/:id', component: EditSvComponent, canActivate: [AuthManager, AdminGuard]},
  {path: 'edituser/:id', component: EditUserComponent, canActivate: [AuthManager]},
  {path: '**', redirectTo: '/login', pathMatch: 'full'}
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule]
})

export class RoutingModule {
}


