import {Component, OnInit} from '@angular/core';
import {AuthManager} from "../authmanager";

@Component({
  selector: 'app-clear-storage',
  templateUrl: './clear-storage.component.html',
  styleUrls: ['./clear-storage.component.css']
})
export class ClearStorageComponent implements OnInit {

  private showButton: boolean = false;

  constructor(private authManager: AuthManager) {
  }

  ngOnInit() {

    setTimeout(() => {
      //console.log('this.authManager.errMsg =>', this.authManager.errMsg);
      //this.authManager.errMsg ? this.showButton = true : this.showButton = false;
      this.showButton = !!this.authManager.errMsg;
    }, 1000)

  }

  clearStorage() {
    window.localStorage.removeItem('auth_key');
    location.reload();
  }

}



