import {Injectable} from "@angular/core";
import {CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot} from "@angular/router";
import {Location} from '@angular/common';
import {TokenParseService} from "./token-parse.service";

@Injectable()
export class UserGuard implements CanActivate {
  errMsg: string = '';

  constructor(private location: Location, private tokenParse: TokenParseService) {
  }

  canActivate(next: ActivatedRouteSnapshot, state: RouterStateSnapshot) {

    //check role from auth_key
    if (this.tokenParse.getRole() == '3') {
      return true;
    }

    console.log('Access denied! Permission mismatch');
    alert('Access denied! Permission mismatch');

    this.location.back();
    return false;
  }
}


