import {Injectable} from '@angular/core';
import {Router} from "@angular/router";
import {Http, Headers} from "@angular/http";
import {Globals} from "./globals";
import {NavbarComponent} from "./navbar/navbar.component";

@Injectable()
export class LogOutService {

  constructor(private router: Router, private http: Http) {
  }

  logout() {
    let headers = new Headers();
    let body = 'token=' + window.localStorage.getItem('auth_key');

    headers.append('Content-Type', 'application/X-www-form-urlencoded');

    return new Promise((resolve) => {
      this.http.post(Globals.BASE_API_URL + '/offline', body, {headers: headers}).subscribe((data) => {
        resolve(data.json().msg);
      })
    }).then((res) => {
      console.log(res);
      window.localStorage.removeItem('auth_key');
      NavbarComponent.returned.next(false);
      this.router.navigate(['/login']);
    });
  }

  logoutSync() {
    let xhr = new XMLHttpRequest();
    let body = 'token=' + window.localStorage.getItem('auth_key');

    xhr.open("POST", Globals.BASE_API_URL + '/offline', true);
    xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');

    // work only in firefox
    xhr.onreadystatechange = () => {
      if (xhr.readyState === XMLHttpRequest.DONE && xhr.status === 200) {
        // window.localStorage.removeItem('auth_key');
        // this.router.navigate(['/login']);
        console.log(xhr.responseText);
      }
    };

    xhr.send(body);
  }
}



