import {Component, OnInit} from '@angular/core';
import { RestService } from "../rest.service";

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.css']
})

export class EmployeeComponent implements OnInit {

  private userSkills: any;
  private statusMsg: string;
  private currentStatus: any;
  private resMsg: string;
  private errMsg: string;
  private dis: boolean = false;

  private logsTime: any[] = [];

  constructor(private restService: RestService) {}

  ngOnInit() {

    let body = 'token=' + window.localStorage.getItem('auth_key');

    this.restService.send('post', '/skill', body)
      .then((res) => {
        let response = res[0];
        if(response.success){

          this.userSkills = response.allData.skills;
          this.currentStatus = this.userSkills[0];

          this.logsTime = response.allData.logs;
          console.log('this.logsTime => ', this.logsTime);

          // Rest satus id = 1, default after log-in
          this.logStatus(1);
        }
        else {
          this.dis = true;
          console.log(response.msg);
          this.errMsg = response.msg;
        }
      })
      .catch((ex) => {
        console.log('error.statusText -> ', ex);
        this.errMsg = ex;
      });

  }

  logStatus (task_id) {
    let token = window.localStorage.getItem('auth_key');
    let body = `token=${token}&task=${task_id}`;

    this.restService.send('post','/log', body)
        .then((res) => {
          //console.log(res);
          let response = res[0];

          if(response.success){
            this.resMsg = response.msg;
            this.statusMsg = 'Your current status is => ' + this.currentStatus.title;

            let curTime = Date.now()/1000;
            this.logsTime.push(
              {
                created_at: curTime,
                task_id: task_id,
                title: this.currentStatus.title
              }
            );
            console.log('this.logsTime => ', this.logsTime);

          }
          else if (!response.success) {
            this.errMsg = response.msg;
          }
        })
        .catch((ex) => {
          console.log('error.statusText -> ', ex);
          this.errMsg = ex;
        });
  }

  onChange(skill: any): void {
    this.currentStatus = skill;
  }

  confirm() {
    this.logStatus(this.currentStatus.id);
  }
}



