import {Injectable} from '@angular/core';
import {Http, Headers} from "@angular/http";
import {Globals} from "./globals";

@Injectable()
export class RestService {

  constructor(private http: Http) {
  }

  public send(req_method: string, req_path: string, req_body?: string, content_type?: string) {

    if (!content_type) content_type = 'application/X-www-form-urlencoded';

    let headers = new Headers();
    headers.append('Content-Type', content_type);

    return new Promise((resolve, reject) => {
      switch (req_method) {
        case 'get': {
          this.http.get(Globals.BASE_API_URL + req_path).subscribe(
            (data) => {
              resolve([data.json()]);
            },
            (error) => {
              console.log(error.toString());
              reject(error.statusText);
            }
          );
          break;
        }

        case 'post': {
          this.http.post(Globals.BASE_API_URL + req_path, req_body, {headers: headers}).subscribe(
            (data) => {
              resolve([data.json()]);
            },
            (error) => {
              console.log(error.toString());
              reject(error.statusText);
            }
          );
          break;
        }

        case 'put': {
          this.http.put(Globals.BASE_API_URL + req_path, req_body, {headers: headers}).subscribe(
            (data) => {
              resolve([data.json()]);
            },
            (error) => {
              console.log(error.toString());
              reject(error.statusText);
            }
          );
          break;
        }

      }
    })

  }
}



