import { Component, OnInit } from '@angular/core';
import { RestService } from "../rest.service";

@Component({
  selector: 'app-inactive-list',
  templateUrl: './inactive-list.component.html',
  styleUrls: ['./inactive-list.component.css']
})
export class InactiveListComponent implements OnInit {

  private users: any;
  private resMsg: string;
  private sure: boolean;

  constructor(private restService: RestService) {
  }

  ngOnInit() {
    this.restService.send('get', '/users')
        .then((res) => {
          //console.log(res);
          let response = res[0];

          if (response.success) {
            this.users = response.users;
          }
          else {
            this.resMsg = response.msg;
          }
        })
        .catch((ex) => {
          console.log('error.statusText -> ', ex);
          this.resMsg = ex;
        });
  }

  activateUser(user: any) {
    let user_id = user.id;
    console.log('user_id => ', user_id);

    this.sure = confirm(`Do you really want to change state of ${user.first_name} ${user.last_name} to active ?`);

    if (this.sure) {
      this.restService.send('put', `/deleteuser/:${user_id}:1`)
          .then((res) => {
            //console.log(res);
            let response = res[0];

            this.resMsg = response.msg;

            if (response.success) {
              console.log(this.resMsg);
              this.ngOnInit();
              setTimeout(() => location.reload(), 500)
            }
          })
          .catch((ex) => {
            console.log('error.statusText -> ', ex);
            this.resMsg = ex;
          });
    }

  }

}



