import {Component, OnInit} from '@angular/core';
import {Router} from "@angular/router";
import {TokenParseService} from "../token-parse.service";
import {Subject} from "rxjs";
import {AuthManager} from "../authmanager";

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  userRole: string;
  userName: string[];
  // private authError: boolean = false;

  public static returned: Subject<any> = new Subject();

  constructor(private router: Router,
              private tokenService: TokenParseService,
              private authManager: AuthManager) {
    NavbarComponent.returned.subscribe(res => {
      this.ngOnInit()
    });
  }

  ngOnInit() {
    this.userName = ['', ''];
    this.userRole = '';

    if (window.localStorage.getItem('auth_key')) {
      this.userName = this.tokenService.getFullName();
      this.userRole = this.tokenService.getRole();
    }
    // this.authError = !!this.authManager.errMsg
  }

  addUser() {
    this.router.navigate(['/adduser']);
  }

  editSkills() {
    if (this.userRole == "1") {
      this.router.navigate(['/editskills']);
    }
    else {
      this.router.navigate(['/sveditskills']);
    }
  }

  goHome() {
    if (this.userRole == "1") {
      this.router.navigate(['/admin']);
    }
    else if (this.userRole == "2") {
      this.router.navigate(['/supervisor']);
    }
  }

  listAllUsers() {
    if (this.userRole == "1") {
      this.router.navigate(['/listall']);
    } else {
      this.router.navigate(['/svlistall'])
    }
  }
}



