import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {Location} from '@angular/common';
import 'rxjs/add/operator/switchMap';
import {User} from "../user";

import {TokenParseService} from "../token-parse.service";
import {RestService} from "../rest.service";

@Component({
  selector: 'app-edit-user',
  templateUrl: './edit-user.component.html',
  styleUrls: ['./edit-user.component.css']
})
export class EditUserComponent implements OnInit {

  private user_id: number;
  private currentUser: any;
  private currentUserSkills: any;
  private currentUserTeam: number;
  private splicedTeam: any;
  private errMsg: string;
  private resMsg: string = '';
  private isAdmin: boolean;
  private teams: any[] = [];
  private tasks: any[] = [];
  private tasksMap: any = {};
  private checkedTasks: any[] = [];

  public user: User = {
    id: 0,
    f_name: '',
    l_name: '',
    email: '',
    password: '',
    team: 0,
    skill_set: []
  };

  constructor(private route: ActivatedRoute,
              private location: Location,
              private tokenParse: TokenParseService,
              private restService: RestService) {
  }


  goBack(): void {
    this.location.back();
  };


  public updateCheckbox(skill, event) {
    this.tasksMap[skill.id] = event.target.checked;
  };


  public updateTasks() {
    for (let x in this.tasksMap) {
      if (this.tasksMap[x]) {
        this.checkedTasks.push(x);
      }
    }
  };


  ngOnInit(): void {
    this.isAdmin = this.tokenParse.getRole() == '1';
    // console.log('this.isAdmin => ', this.isAdmin);

    //getting info about editing user
    this.route.params.subscribe(params => {
      this.user_id = +params["id"];
    });
    // console.log('this.user_id => ', this.user_id);

    let body = 'user_id=' + this.user_id;

    this.restService.send('post', '/userinfo', body).then((res) => {
      //console.log(res);
      let response = res[0].allData;

      if (res[0].success) {
        this.currentUser = response.data;
        this.currentUserSkills = response.skills;
        this.currentUserTeam = this.currentUser.team;

        this.user = {
          id: this.user_id,
          f_name: this.currentUser.first_name,
          l_name: this.currentUser.last_name,
          email: this.currentUser.email,
          password: this.currentUser.password
        };

        this.restService.send('get', '/getdata').then((res) => {
          //console.log('res => ', res);
          let result = res[0].allData;
          if (res[0].success) {
            this.teams = result.teams;

            for (let index in this.teams) {
              let i = Number(index);

              if (this.teams[i].id == this.currentUserTeam) {
                this.splicedTeam = this.teams.splice(i, 1)[0];
                this.teams.unshift(this.splicedTeam);
                break;
              }
            }

            //setting default value for select
            this.user.team = this.teams[0].id;

            this.tasks = result.tasks;

            //checking checkboxes with user skills
            for (let skill of this.currentUserSkills) {
              this.tasksMap[skill.id] = true;
            }
            //console.log('this.tasksMap => ', this.tasksMap);
          } else {
            this.errMsg = res[0].msg;
          }
        }).catch((ex) => {
          console.log('error.statusText -> ', ex);
          this.errMsg = ex;
        });
      }
      else {
        this.errMsg = res[0].msg;
      }
    }).catch((ex) => {
      console.log('error.statusText -> ', ex);
      this.errMsg = ex;
    });

  } //onInit();


  public save(user: User, isValid: boolean) {
    // console.log('isValid => ', isValid);

    if (isValid) {
      this.updateTasks();
      user.skill_set = this.checkedTasks;
      this.checkedTasks = [];

      // this.isAdmin ? user.team = +user.team : user.team = this.user.team;
      user.team = this.isAdmin ? +user.team : this.user.team;

      user.id = this.user_id;

      let body = `user=${JSON.stringify(user)}`;
      this.restService.send('put', '/updateuser', body).then((res) => {

        let response = res[0];
        this.resMsg = response.msg;

        if (response.success) {
          setTimeout(() => {
            this.goBack()
          }, 2000);
        }

        setTimeout(() => {
          this.resMsg = ''
        }, 5000)
      }).catch((ex) => {
        console.log('error.statusText -> ', ex);
        this.errMsg = ex;
      });
    }
  };

}



